.titleBar{
    background-color: #333;
    display: block;
    color: #fff;
    padding-left: 50px;
}
.header{
    font-family: 'Gruppo', cursive;
    padding-left: 30px;
    font-size: 39px;
}

.cat{
    font-family: 'Fredericka the Great', cursive;;
    font-size: 75px;
    background: #C2DFFF;
}
.appbar{
    background: #C2DFFF;
}

.vibes{
    font-size: 100px;
    padding-top: 100px;
    font-family: 'Poiret One', cursive;
    font-weight: bold;
    padding-left: 15px;
}

.App {
    text-align: center;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}